.main-view {
    ul, ol {
        padding-left: 2rem;
    }

    .feedback-slide {
        width: 100%;
        height: 100%;
        padding: 2rem 2rem 0 2rem;
        display: flex;
        align-items: center;
        font-size: 1.2rem;
        justify-content: center;
        position: relative;
        overflow: hidden;

        .tabbed {
            margin-left: 2rem;
        }

        .feedback-block {
            padding: 0 2rem;
            flex-basis: 50%;
            overflow: hidden;
            position: relative;
            max-height: 100%;
            display: flex;
            flex-direction: column;
        }

        .examine-action-hint {
            position: absolute;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
        }

        // First pane
        .feedback-prompt {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 1rem;
            height: 100%;
            overflow: scroll;

            .rating-btns {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 1rem;

                .selected-rating {
                    border: 3px solid black;
                }

                button {
                    &:hover {
                        background-color: #eee;
                    }
                }
            }
        }

        // Middle Feedback pane 
        .analysis {
            .feedback-list {
                height: 100%;
                overflow: scroll;

                li.active-examine {
                    list-style-type: 'âž¤'; // ***** Active examine pointer
                    color: rgb(47, 206, 47);
                    padding-left: 0.5rem;
                }
            }
        }

        // Examine pane
        .feedback-form {
            display: flex;
            flex-direction: column;
            // align-items: center;
            // justify-content: center;
            // text-align: center;

            li {
                // padding: 0.25rem 0;
            }

            textarea {
                width: 80%;
                padding: 2px 3px;
                margin-top: 0.5rem;
                // align-self: center;
            }
        }
    }
}